.Buttons {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 7px; }

.Button {
  width: auto;
  border-radius: 5px;
  font-size: 18px;
  padding: 4px;
  background-color: #66ffff;
  -webkit-box-shadow: 0 5px 5px  #66ccff , 0 5px 10px  #0000ff;
          box-shadow: 0 5px 5px  #66ccff , 0 5px 10px  #0000ff; }
  .Button:hover {
    background-color: #bf00ff;
    border: 2px solid white;
    color: #ffffff; }
  .Button:active {
    color: #ffff4d;
    padding: 0;
    background-color: inherit;
    font-size: 1.5em; }



