.Screen {
  border: 1px solid #e6e6ff;
  margin: 5px;
  border-radius: 5px; }

.no-arc, .arc {
  padding: 3px;
  margin: 4px 10px;
  width: 30px;
  color: #4d0026;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s; }

.arc {
  color: #ffff00;
  -webkit-box-shadow: 0 12px 16px 2px  #ffffff,0 10px 20px 0  #ffff1a;
          box-shadow: 0 12px 16px 2px  #ffffff,0 10px 20px 0  #ffff1a;
  border-radius: 5px; }

.screen-key {
  display: -ms-flexbox;
  display: flex; }

.open-panel {
  display: block;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: #ffccff;
  font-weight: bold;
  font-size: 1.1em;
  border-radius: 5px;
  padding: 3px;
  margin: 3px; }

.open-panel:hover {
  outline: none;
  -webkit-box-shadow: 0 2px 10px 2px #ffffff,0 5px 10px 0 #4d0026;
          box-shadow: 0 2px 10px 2px #ffffff,0 5px 10px 0 #4d0026;
  -webkit-transition-duration: 0.7s;
       -o-transition-duration: 0.7s;
          transition-duration: 0.7s; }

.open-panel:active {
  background-color: #0000ff;
  font-weight: bold;
  color: #ffff00;
  outline: none;
  -webkit-transition-duration: 0.1s;
       -o-transition-duration: 0.1s;
          transition-duration: 0.1s;
  border: 2px solid #0000ff;
  -webkit-box-shadow: 0 2px 20px 0 #0000ff,0 5px 20px 0 #ffffff;
          box-shadow: 0 2px 20px 0 #0000ff,0 5px 20px 0 #ffffff; }



